.category-button{
    height: 200px;
    width: 395px;
    background: #f4791bf7;
    margin: 20px;
    float: left;
    border:0px;
    box-shadow: 0 0 1px #ccc;
    transform-origin: 50% 50%;
    -webkit-transform-origin: 50% 50%;
    transition-duration: 1s;
    -webkit-transition-duration: 1s;
    transition-timing-function: ease-out;
    -webkit-transition-timing-function: ease-out;
    -webkit-box-shadow: 200px 0px 0 0 lightseagreen inset , -200px 0px 0 lightseagreen inset;
}
.category-button:hover{
    color:#fff;
    font-size: 100px !important;
    box-shadow: 0px 0px 0 0 lightseagreen inset , 0px 0px 0 lightseagreen inset;
    -webkit-box-shadow: 0px 0px 0 0 lightseagreen inset , 0px 0px 0 lightseagreen inset;
}